
*{
  margin:0;
  padding:0;
  box-sizing:border-box;
  font-family:Nunito;
  color:#fff;
}

.container{
  width:80%;
  margin:auto;
}

.App{
  height:100vh;
  width:100%;
}

.title{
  padding:20px 0 40px 0;
  color:#fff;
}

.weather-temp{
  padding-bottom:40px;
  padding-left:60px;
}

.weather-temp h1 , .weather-temp h4 , .weather-temp p{
  padding-bottom: 10px;
}

.weather-info p , .weather-info h2{
  text-align:center;
}

.user-weather{
  color:#fff;
  padding:30px;
  border-radius: 10px;
  border:1px solid #fff;
}

.navbar{
  background-color:#ddd;
}

.mainImg{
  padding-top:15px;
}

.region{
  margin-top:30px;
}

.region-input{
  background:none;
  border:none;
  border-bottom:1px solid #fff;
  padding:0 50px 0 0px;
  outline:none;
}

.region-input::placeholder{
  font-family:Nunito;
  font-weight:600;
  font-size:1rem;
  padding-left:0;
  color:rgba(255, 255, 255, 0.63);
}